@mixin table-scrollbar() {
  @include perfect-scrollbar(var(--scrollbar-color), 5px, 3px);
}

.table-wrapper {
  --table-wrapper-title-color: #{$dark-blue};
  --text-truncate-lines: 2;

  @include flex-column;
  height: 100%;
  container-type: inline-size;
  overflow: hidden;

  &--no-pagination {
    .pagination {
      display: none;
    }
  }

  &:has(.ag-root-wrapper) {
    .table-wrapper__header::after {
      display: none !important;
    }

    .modal & {
      max-height: 500px;
      height: calc(var(--ag-rows-count) * #{$table-row-height} + 99px);
    }

    @include mobile {
      height: calc(var(--ag-rows-count) * #{$table-row-height} + 99px);
    }
  }
}

@include color-mode(dark) {
  .table-wrapper {
    --table-wrapper-title-color: #{$light-blue};
  }
}

@include color-mode(light-contrast) {
  .table-wrapper {
    --table-wrapper-title-color: #{$con-dark-blue};
  }
}

@include color-mode(dark-contrast) {
  .table-wrapper {
    --table-wrapper-title-color: #{$white};
  }
}

@include contrast-themes {
  .table-wrapper {
    .icon-checkbox, .icon-btn {
      color: currentColor !important;
    }
  }
}

.table-wrapper__body {
  @include hide-scroll;
  position: relative;
  flex-grow: 1;

  @include table-scrollbar;

  > .ps__rail-y {
    margin-top: $table-header-height;
  }
}