// -*- scope: emm; createEmpty: true-*-

$detached-btn-space: 40px;

#layout-builder-view {
  > .tile .tile-header {
    flex-basis: 50px;
  }

  #building-blocks-tile {
    flex: 0 1 15vw;
    min-width: 10vw;
  }

  #div-child-edit-tile {
    flex: 0 1 25vw;
    min-width: 10vw;
  }

  #grid-template-tile {
    flex: 1 0 800px;

    @media (max-width: 1279px) {
      flex-shrink: 1;
    }

    .ps__rail-x, .ps__rail-y {
      z-index: 10000000;
    }
  }
}

body:has(#layout-builder-view) {
 #server-undo-option {
   display: none;
 }
}

#lb-grid-template {
  .tmpl-cell {
    height: 80px;
    padding: 0;
    margin: 1px 0;
    background: #ABABAB;

    &.container-hover-accept {
      border: 2px dashed $blue-var;
      background-image: repeating-linear-gradient(-45deg, transparent, transparent 5px, rgba(174,199,209,0.5) 5px, rgba(174,199,209,0.5) 10px);
    }
  }

  .container-row {
    display: flex;
    overflow: auto;
  }
}

#lb-sidebar {
  flex: none;
  position: relative;

  .btn {
    position: absolute;
  }

  .btn--small {
    height: 21px;
    border-radius: 5px;
  }

  .btn--attached {
    @include border-start-radius(0);
    border-left: none;
  }

  .btn--detached {
    left: $detached-btn-space;

    &:before {
      content: '';
      position: absolute;
      border-top: 1px solid;
      border-color: inherit;
      width: $detached-btn-space;
      left: -$detached-btn-space;
    }
  }
}

#lb-editor-wrapper {
  display: flex;
  justify-content: space-between;
  width: min-content;
}

#lb-template-wrapper {
  flex-grow: 1;
  position: relative; // prevents the div child from moving out when resizing the window
}

#lb-editor-progress {
  position: absolute;
  inset: 0;
  background-color: $body-bg-var;
  z-index: 1000;
}

.badge--lb-target {
  @extend .badge--lg;
  background-color: $blue;
}

@include color-mode(light-contrast) {
  .badge--lb-target {
    color: $white;
    background-color: $con-dark-blue;
  }
}

@include color-mode(dark-contrast) {
  .badge--lb-target {
    color: $con-dark-blue;
    background-color: $white;
    border-color: $con-dark-blue;
  }
}

.div-sandbox-overlay {
  position: absolute;
  inset: 0;

  .highlighted & {
    opacity: 0.3;
    background-color: $warning-var
  }
}

.highlight-border:before {
  content: '';
  display: block;
  position: absolute;
  inset: 0;
  box-shadow: inset 0 0 0 2px $light-blue;
}

.div-container {
  color: white;
  word-wrap: break-word;
  position: absolute;
  min-height: 10px;

  img  {
    @include size(auto);
    max-width: none;
    display: block;
  }

  &:not(.ui-resizable-autohide) .div-sandbox-overlay {
    border: 1px dashed $blue-var;

    &.div--deleted {
      border: 2px solid $danger-var;
      > div {
        position: absolute;
        top: -2px;
        left: -2px;

        .btn {
          border-radius: 0 0 10px 0;
        }
      }
    }
  }
}

.div-sandbox {
  display: block;
  @include size(100%);
  border: 0;
  pointer-events: none;
}

.div-container-controls {
  display: none;

  gap: $gap-sm;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  top: 0;
  // bottom defines in JS

  .btn-group {
    width: min-content;
    align-self: flex-end;

    > :first-child {
      border-top-left-radius: 0;
    }

    > :last-child {
      @include border-end-radius(0);
    }
  }

  .lb-layering-btn {
    @include border-start-radius(0);
    border-bottom-right-radius: 0;
  }

  .badge {
    @include border-end-radius(0);
    border-bottom-left-radius: 0;
  }
}

@keyframes elem-entrance {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

.container-row-animated {
  animation: elem-entrance 200ms ease-in-out forwards;
}

.lb-building-blocks {
  @include flex-column($gap);

  > * {
    width: 100%;
  }
}

.grid-building-block {
  --building-block-name-color: #{$dark-grey};
  --building-block-thumbnail-bg: #D9D9D9;

  cursor: move;
  user-select: none;
  color: var(--building-block-name-color);

  @include flex-column($gap-x-sm);

  .building-block-thumb-box {
    @extend .flex-center;
    height: 150px;
    background: var(--building-block-thumbnail-bg);
  }

  img {
    @include max-size(100%);
  }

  &.ui-draggable-dragging {
    background-color: $body-bg-var;
    opacity: 0.5;
  }
}

@include color-mode(dark) {
  .grid-building-block {
    --building-block-name-color: #{$light-grey};
  }
}

@include color-mode(light-contrast) {
  .grid-building-block {
    --building-block-name-color: #{$con-dark-blue};
    --building-block-thumbnail-bg: #{$con-grey};
  }
}

@include color-mode(dark-contrast) {
  .grid-building-block {
    --building-block-name-color: #{$white};
    --building-block-thumbnail-bg: #{$con-grey};
  }
}