// -*- scope: emm; createEmpty: true-*-
#dashboard-analysis-tile {
  .mia-notification {
    --mia-notification-color: #{$dark-grey};
    --mia-notification-date-color: #{$gray-600};

    padding: $gap-sm;
    border: $border-default;
    border-radius: $border-radius;
    width: 100%;
    @include flex-row($gap-sm);
    align-items: center;
    color: var(--mia-notification-color);

    > .icon {
      font-size: 25px;
    }
  }

  .mia-notification__date {
    font-size: $font-size-sm;
    text-align: right;
    color: var(--mia-notification-date-color);
  }

  .mia-notification--positive {
    > .icon {
      color: $success-var;
    }
    border-color: $success-var;
  }

  .mia-notification--negative {
    > .icon {
      color: $danger-var;
    }
    border-color: $danger-var;
  }
}

@include color-mode(dark) {
  #dashboard-analysis-tile .mia-notification {
    --mia-notification-color: #{$light-grey};
    --mia-notification-date-color: #{$light-grey};
  }
}

@include color-mode(light-contrast) {
  #dashboard-analysis-tile .mia-notification {
    --mia-notification-color: #{$con-dark-blue};
    --mia-notification-date-color: var(--mia-notification-color);
  }
}

@include color-mode(dark-contrast) {
  #dashboard-analysis-tile .mia-notification {
    --mia-notification-color: #{$white};
    --mia-notification-date-color: var(--mia-notification-color);
  }
}
