// -*- scope: openemm -*-

#dashboard-calendar {
  --xl-calendar-alien-day-bg: #{$gray-100};
  --xl-calendar-hovered-day-bg: #e5f0f9;
  --xl-calendar-day-header-color: #{$dark-grey};
  --xl-calendar-cell-day-header-color: #{$gray-600};
  --xl-calendar-hovered-day-color: var(--xl-calendar-cell-day-header-color);
  --xl-calendar-cell-border-color: #{$light-grey};
  --xl-calendar-today-border-color: #{$blue};
  --xl-calendar-label-color: #{$dark-grey};

  .tile-body {
    display: grid;
    grid-template-columns: 1fr minmax(200px, 300px);
    gap: $gap-md;
  }

  #dashboard-calendar {
    min-height: 0;

    #dashboard-calendar-table       { grid-area: days; }
    #dashboard-calendar-weekdays    { grid-area: weekdays; }
    #dashboard-calendar-weeks       { grid-area: weeknumbers; }

    display: grid;
    grid-template-areas:
        'weeknumbers weekdays'
        'weeknumbers days    ';
    grid-template-columns: auto 3fr;
    grid-template-rows:    auto 1fr;

    #dashboard-calendar-weeks {
      @include flex-column(0);
      padding-top: 25px; // weekdays height
      align-items: center;
      margin-right: $gap-x-sm;

      div {
        @include font($font-size-md, $font-weight-medium);
        color: var(--xl-calendar-day-header-color);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-grow: 1;
      }
    }

    #dashboard-calendar-weekdays {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      align-items: center;
      margin-bottom: $gap-x-sm;

      div {
        @include font($font-size-md, $font-weight-medium);
        color: var(--xl-calendar-day-header-color);
        text-align: center;
      }
    }

    #dashboard-calendar-table {
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      grid-auto-rows: minmax(min-content, auto);
      @include border($border-radius);
      position: relative;


      // background-color + gap = border
      background-color: var(--xl-calendar-cell-border-color);
      gap: 1px;
    }

    .dashboard-calendar-day {
      @extend .unselectable;
      @include flex-column(0);
      position: relative;
      background-color: $body-bg-var;
      padding-bottom: $gap-sm;

      &:first-child {
        @include border-start-radius;
      }

      &:last-child {
        @include border-end-radius;
      }

      &__header {
        display: flex;
        justify-content: space-between;
        font-weight: $form-label-font-weight;
        color: var(--xl-calendar-cell-day-header-color);
        padding: $gap-sm;
      }

      &__body {
        position: relative;
        @include flex-column($gap-x-sm);
        @include perfect-scrollbar(var(--scrollbar-color), $gap-x-sm, 2px);
        padding-inline: $gap-sm;
        flex-grow: 1;
      }

      &--alien {
        background-color: var(--xl-calendar-alien-day-bg);
      }

      &--today {
        border: 2px solid var(--xl-calendar-today-border-color);
      }

      &:hover, &--hovered {
        background-color: var(--xl-calendar-hovered-day-bg);
        --xl-calendar-cell-day-header-color: var(--xl-calendar-hovered-day-color);

        [data-action="create-dashboard-calendar-comment"] {
          display: block;
        }
      }
    }

    [data-action="create-dashboard-calendar-comment"] {
      display: none;
      text-align: right;
      font-size: $gap-sm;
      color: $blue;
    }
  }

  &:has(#dashboard-calendar-mode-switch:not(:checked)) {
    #dashboard-calendar .dashboard-calendar-day {
      &:first-child {
        border-bottom-left-radius: 0;
      }

      &:last-child {
        border-top-right-radius: 0;
      }
    }
  }

  [data-action="load-more-day-mailings"] {
    color: $white;
    text-align: center;
    span {
      border-color: $blue !important;
    }
  }
}

.dashboard-calendar-label {
  display: flex;
  outline: none;
  overflow: hidden;
  border-radius: 5px;
  min-height: fit-content;

  span:first-child {
    @include flex-center;
    width: 20px;
    height: 100%;
    border-radius: 0;
  }

  span:last-child {
    color: var(--xl-calendar-label-color);
    --text-truncate-lines: 2;
    @include text-truncate-lines;
    min-height: 24px;
    padding-inline: $gap-x-sm;
    font-size: $font-size-sm;
    flex-grow: 1;
    align-content: center;
  }

  &__text { // todo remove

  }
}

@each $state, $values in $mailing-status-badges-defs {
  .dashboard-calendar-label--mailing\.status\.#{$state} {
    $background: map-get(map-get($mailing-status-badges-defs, $state), background);
    $icon: map-get(map-get($mailing-status-badges-defs, $state), icon);

    span:first-child {
      background-color: $background;
      &:before {
        content: fa-content($icon);
      }
    }

    span:last-child { // 30% transparent background
      position: relative;
      &::before {
        content: "";
        background: $background;
        position: absolute;
        inset: 0;
        opacity: 0.3;
      }
    }
  }
}

.dashboard-calendar-label--comment {
  span:first-child {
    background-color: $stat-light-blue;
  }
  span:last-child {
    background-color: rgba($stat-light-blue, 0.3);
  }
}

@include color-mode(dark) {
  #dashboard-calendar {
    --xl-calendar-alien-day-bg: #{$dark-grey};
    --xl-calendar-hovered-day-bg: #{$grey-blue};
    --xl-calendar-cell-day-header-color: #{$white};
    --xl-calendar-day-header-color: #{$light-grey};
    --xl-calendar-label-color: #{$x-light-grey};
  }
}

@include color-mode(light-contrast) {
  #dashboard-calendar {
    --xl-calendar-hovered-day-bg: #{$con-light-blue};
    --xl-calendar-cell-day-header-color: #{$con-dark-blue};
    --xl-calendar-cell-border-color: #{$con-dark-blue};
    --xl-calendar-today-border-color: #{$con-dark-blue};
  }
}

@include color-mode(dark-contrast) {
  #dashboard-calendar {
    --xl-calendar-alien-day-bg: #{$dark-grey};
    --xl-calendar-hovered-day-color: #{$white};
    --xl-calendar-hovered-day-bg: #{$con-light-blue};
    --xl-calendar-cell-day-header-color: #{$white};
    --xl-calendar-day-header-color: #{$white};
    --xl-calendar-cell-border-color: #{$white};
    --xl-calendar-today-border-color: #{$con-grey};
  }
}
