// -*- scope: emm; createEmpty: true -*-

#inbox-preview-tiles {
  display: grid;
  gap: $gap-md;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.inbox-preview-card {
  --tile-title-color: #{$white};

  &__logo-title {
    display: flex;
    gap: $gap-x-sm;
    align-items: center;
  }

  .tile-header {
    img {
      @include size($gap);
    }
  }

  .tile-body {
    @include flex-column($gap-sm);
    padding: $gap-sm;

    label {
      @extend .text-truncate;
      font-weight: normal;
      color: $body-color-var;
    }
  }
}

@include color-mode(dark) {
  .inbox-preview-card {
    --tile-title-color: #{$white};
  }
}

@include contrast-themes {
  .inbox-preview-card {
    --tile-title-color: #{$con-dark-blue};
  }
}

.inbox-preview-result-client-card {
  @extend .inbox-preview-card;

  .tile-body {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  }
}

.mailing-preview-result-card {
  @extend .card;
  border: $border-transparent;
  border-radius: 0;
  &:hover {
    border: 1px solid $blue-var;
  }

  .card-body {
    padding: $gap-sm;
  }

  .card-title {
    display: flex;
    align-items: center;
    gap: $gap-x-sm;
    margin-bottom: $gap-x-sm !important;
  }

  .preview-not-supported-msg {
    height: 90px;
    @include flex-center;
    text-align: center;
  }

  .inbox-preview-result-image {
    width: 120px;
  }
}

$browser-defs:(
  edge:    #0CF,
  firefox: #EE8424,
  opera:   #cc0f16,
  safari:  #08c,
  chrome:  #FFA700,
  desktop: #54DAFF,
  mobile:  #A4C639,
  spam:    #888
);

@each $state, $color in $browser-defs {
  .inbox-preview-card--#{$state} {
    border-color: $color;
    .card-header {
      background-color: $color;
    }
    .icon-badge {
      background-color: $color;
    }
  }
}
