// -*- scope: openemm -*-

.dashboard-mailing-popover {
  border-radius: $gap-x-sm;
  width: 200px;
  max-width: 200px;
  font-size: $font-size-sm;

  h1 {
    --tile-title-color: #{$dark-blue};
    font-size: $font-size-base !important;
  }

  .popover-body {
    span {
      color: var(--label-color);
      @include flex-row($gap-x-sm);
      align-items: center;
      font-weight: 500;
    }
    p {
      color: $gray-600;
    }
  }
}

@include color-mode(dark) {
  .dashboard-mailing-popover h1 {
    --tile-title-color: #{$light-blue};
  }
}


@include color-mode(light-contrast) {
  .dashboard-mailing-popover h1 {
    --tile-title-color: #{$con-dark-blue};
  }
}

@include color-mode(dark-contrast) {
  .dashboard-mailing-popover h1 {
    --tile-title-color: #{$white};
  }
}
