// -*- scope: emm; createEmpty: true -*-

.grid-preview {
  --grid-preview-column-bg: rgba(0, 113, 185, 0.3);
  --grid-preview-gutter-bg: #{$light-grey};
  --grid-preview-empty-column-bg: rgba(0, 113, 185, 0.1);
  --grid-preview-color: #{$white};

  display: flex;
  justify-content: center;
  @include size(100%);
  color: var(--grid-preview-color);

  &__column {
    background-color: var(--grid-preview-column-bg);
    overflow: hidden;

    &--empty {
      background-color: var(--grid-preview-empty-column-bg);
    }
  }

  &__gutter {
    background-color: var(--grid-preview-gutter-bg);
  }
}

@include color-mode(light-contrast) {
  .grid-preview {
    --grid-preview-column-bg: #{$con-dark-blue};
    --grid-preview-gutter-bg: #{$con-grey};
    --grid-preview-empty-column-bg: #{$con-blue};
  }
}

@include color-mode(dark-contrast) {
  .grid-preview {
    --grid-preview-column-bg: #{$white};
    --grid-preview-gutter-bg: #{$con-blue};
    --grid-preview-empty-column-bg: #{$con-grey};
    --grid-preview-color: #{$con-dark-blue};
  }
}