// -*- scope: emm; createEmpty: true -*-

$chat-message-arrow-width: 30px;
$chat-spacer: $gap-md;
$chat-msg-gap: $gap-x-sm;
$chat-icon-size: 30px;

.chat {
  --chat-icon-bg: #{$dark-grey};
  --chat-icon-color: #{$white};
  --chat-icon-border-color: transparent;

  --chat-message-bg: #{$light-grey};
  --chat-message-color: #{$black};
  --chat-message-border-color: var(--chat-message-bg);

  --chat-message-out-bg: transparent;
  --chat-message-out-color: #{$blue};
  --chat-message-out-border-color: #{$blue};
  --chat-message-out-icon-bg: var(--chat-message-out-border-color);
  --chat-message-out-icon-color: #{$white};

  @include flex-column;
  padding-block: $chat-spacer;
  @include border-radius;
  border: $border-default;
  height: 100%;
  min-width: 450px;

  .chat-header {
    @include flex-row($chat-msg-gap);
    align-items: center;
    padding: 0 $chat-spacer $chat-spacer $chat-spacer;
    border-bottom: $border-default;
  }

  .chat-content {
    @include flex-column($chat-spacer);
    align-items: center;
    flex-grow: 1;
    padding: $chat-spacer $chat-spacer 0 $chat-spacer;
    position: relative; // for perfect scrollbar
  }

  .chat-footer {
    @include flex-row($gap-sm);
    padding: $chat-spacer $chat-spacer 0 $chat-spacer;
  }

  .chat__icon-container {
    flex: none;
    @include flex-center;
    background: var(--chat-icon-bg);
    color: var(--chat-icon-color);

    @include size($chat-icon-size);

    border: 1px solid var(--chat-icon-border-color);
    border-radius: 50%;
  }

  .chat__icon {
    font-size: $font-size-base;
  }

  .chat__typing-icon {
    animation: typing 1s infinite;

    @keyframes typing {
      0%, 80%, 100% {
        opacity: 0;
      }
      40% {
        opacity: 1;
      }
    }
  }

  .chat__message {
    @include flex-row($chat-msg-gap);
    flex-wrap: wrap;
    width: 100%;
    max-width: 665px;
    row-gap: $gap-sm !important;

    .message__content {
      width: 0;
      flex-grow: 1;
      padding: $gap-sm;
      @include border-radius;
      border: 1px solid;
      word-break: break-word;

      &:empty {
        height: 40px;
      }
    }
  }

  .chat__message--arrow {
    gap: $chat-msg-gap + $chat-message-arrow-width;

    .message__content {
      position: relative;

      &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        top: -1px;
      }

      &:before {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        top: -1px;
      }
    }
  }

  .chat__message--in {
    padding-right: $chat-msg-gap + $chat-icon-size;

    .message__content {
      background: var(--chat-message-bg);
      border-color: var(--chat-message-border-color);
      color: var(--chat-message-color);
    }

    &.chat__message--arrow .message__content {
      border-top-left-radius: 0;

      &:before {
        border-top: $chat-message-arrow-width + 2 solid var(--chat-message-border-color);
        border-left: $chat-message-arrow-width + 1 solid transparent;
        left: -$chat-message-arrow-width - 2;
      }

      &:after {
        top: 0;
        border-top: $chat-message-arrow-width solid var(--chat-message-bg);
        border-left: $chat-message-arrow-width solid transparent;
        left: -$chat-message-arrow-width;
      }
    }
  }

  .chat__message--out {
    justify-content: flex-end;
    padding-left: $chat-msg-gap + $chat-icon-size;

    --chat-icon-color: var(--chat-message-out-icon-color);
    --chat-icon-bg: var(--chat-message-out-icon-bg);
    --chat-icon-border-color: transparent;

    .message__content {
      background: var(--chat-message-out-bg);
      border-color: var(--chat-message-out-border-color);
      color: var(--chat-message-out-color);
    }

    &.chat__message--arrow .message__content {
      border-top-right-radius: 0;
      &:after {
        border-top: $chat-message-arrow-width solid;
        border-right: $chat-message-arrow-width solid transparent;
        right: -$chat-message-arrow-width;
        color: var(--chat-message-out-border-color);
      }
    }
  }
}

@include color-mode(dark) {
  .chat {
    --chat-icon-bg: #{$gray-600};
    --chat-icon-color: #{$white};
    --chat-message-bg: #{$gray-600};
    --chat-message-color: #{$white};
    --chat-message-out-color: #{$con-light-blue};
  }
}

@include color-mode(light-contrast) {
  .chat {
    --chat-icon-bg: #{$con-dark-blue};
    --chat-icon-color: #{$white};

    --chat-message-border-color: #{$con-dark-blue};
    --chat-message-bg: #{$con-grey};

    --chat-message-out-bg: #{$con-light-blue};
    --chat-message-out-color: #{$con-dark-blue};
    --chat-message-out-border-color: #{$con-dark-blue};
  }
}

@include color-mode(dark-contrast) {
  .chat {
    --chat-icon-bg: #{$dark-grey};
    --chat-icon-color: #{$white};
    --chat-icon-border-color: #{$white};

    --chat-message-border-color: #{$white};
    --chat-message-color: #{$white};
    --chat-message-bg: #{$dark-grey};

    --chat-message-out-color: #{$con-light-blue};
    --chat-message-out-border-color: #{$con-light-blue};
    --chat-message-out-icon-color: #{$con-dark-blue};
  }
}
